.footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 20px;
  border-top: 1px solid var(--border-color);
  background-color: var(--main-bg-color); }

.form {
  text-align: center;
   :global(.adm-list-default .adm-list-body) {
    border-top: none;
    border-bottom: none; }
   :global(.adm-form-item-required-asterisk) {
    display: none; } }

.label {
  color: var(--form-label-color);
  font-size: 16px;
  font-weight: 400;
  margin: 20px 0 14px;
  line-height: 20px; }

.input {
  border: 1px solid var(--form-label-color);
  border-radius: 2px;
  color: var(--main-text-color);
  box-sizing: border-box;
  padding: 7px 0 7px 8px;
  font-size: 16px;
  > input {
    text-align: center; } }
.tip {
  margin-top: 4px;
  color: var(--main-warn-color);
  font-size: var(--main-text-size);
  display: flex;
  align-items: center;
  justify-content: center; }
.warning-icon {
  display: inline-block;
  width: 15px;
  height: 15px;
  margin-right: 4px;
  background: url('../../../assets/images/warning@2x.png') no-repeat center center;
  background-size: contain; }

.button {
  min-width: 120px; }
