.item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: var(--main-text-size);
  line-height: 21px;
  font-weight: 400;
  margin: 10px 16px 10px; }

.item-label {
  color: var(--main-gray-text-color); }

.item-value {
  color: var(--main-text-color); }

.header-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 16px;
  border-bottom: 1px solid var(--main-border-color); }

.user-icon {
  width: 14px;
  height: 16px;
  background: url('../../../assets/images/user@2x.png') no-repeat center center;
  background-size: contain; }

.header-name-wrapper {
  display: flex;
  font-weight: 400;
  align-items: center;
  color: var(--main-text-color); }
.header-name {
  margin-left: 3px; }

.form-header {
  line-height: 24px;
  color: var(--main-text-color);
  font-size: 16px;
  font-weight: 500;
  margin: 10px 16px 6px; }
